// Checkbox.tsx
import clsx from 'clsx';
import { focusRing } from './../../../tailwind/styles/focus';
import { FunctionalIcon } from '../icon/Icon';
import { CheckboxProps } from './checkbox.interface';

export const Checkbox: React.FC<CheckboxProps> = ({
  id,
  checked = false,
  disabled = false,
  error = false,
  label,
  onChange,
  ...props
}) => {
  return (
    <span
      className={clsx(
        'zep-group',
        'zep-relative',
        'zep-inline-flex',
        'zep-flex-row',
        'zep-flex-wrap',
        'zep-items-center',
        'zep-justify-start',
        {
          '[&>svg]:hover:zep-bg-neutral-dark-hover': !disabled && !error,
        },
        { 'zep-cursor-not-allowed': disabled },
      )}
      data-testid="zep-checkbox"
    >
      {checked && (
        <FunctionalIcon
          name="check"
          className={clsx(
            'zep-text-neutral-dark-contrast',
            'zep-rounded-4',
            'zep-w-1.5',
            'zep-h-1.5',
            'zep-z-10',
            'zep-absolute',
            'zep-top-[-0.1px]',
            'zep-left-[-0.1px]',
            { 'zep-bg-transparent': !error && !disabled },
            { 'zep-border-opacity-disabled zep-bg-opacity-disabled': disabled },
            { 'zep-bg-error': error },
          )}
        />
      )}
      <input
        id={id}
        type="checkbox"
        checked={checked}
        onChange={!disabled ? onChange : undefined}
        className={clsx(
          focusRing,
          'zep-rounded-4',
          'zep-appearance-none',
          'focus-visible:zep-ring-offset-1',
          'zep-w-1.5',
          'zep-h-1.5',
          'zep-rounded-4',
          'zep-border-1',
          { 'zep-border-none zep-bg-neutral-dark-default': checked },
          { 'zep-bg-opacity-disabled': checked && disabled },
          { 'zep-border-2 zep-border-error': error },
          { 'zep-border-neutral-dark-default group-hover:zep-border-neutral-dark-hover': !error && !disabled },
          { 'zep-border-neutral-dark-default zep-border-opacity-disabled': disabled },
        )}
        disabled={disabled}
        data-testid="zep-checkbox-input"
        {...props}
      />
      <label
        htmlFor={id}
        className={clsx(
          'zep-body-text',
          'zep-relative',
          'zep-z-20',
          'zep-ml-[-1.5rem]',
          'zep-pl-[36px]',
          { 'zep-text-typography-dark-100': !disabled },
          [disabled ? 'zep-cursor-not-allowed zep-text-opacity-disabled' : 'zep-cursor-pointer'],
        )}
      >
        {label}
      </label>
    </span>
  );
};
